import "./productList.css";
import { DataGrid } from '@material-ui/data-grid';
import { DeleteOutline } from "@material-ui/icons";
import { productRows } from "../../dummyData";
import { Link } from "react-router-dom";
import { useState } from "react";
export default function ProductList() {
    const [data,setData] = useState(productRows)

    const handleDelete = (id)=>{
        setData(data.filter((item) => item.id !== id));
   };


   const columns = [
    { field: 'id', headerName: 'ID', width: 90 },
    { field: 'product', headerName: 'Product', width: 200, renderCell: (parans)=>{
         return (
              <div className="productListItem">
                   <img className="productListImg" src={parans.row.img} alt=""/>
                   {parans.row.name}
              </div>
         )
    } },
    { field: 'stock', headerName: 'Stock', width: 200 },
    {
      field: 'status',
      headerName: 'Status',
      width: 120,
    },
    {
        field: 'price',
        headerName: 'Price',
        width: 160,
      },
      {
           field: "action",
           headerName: "Action",
           width: 150,
           renderCell: (parans)=>{
                return(
                     <>
                     <Link to={"/product/"+parans.row.id}>
                     <button className="productListEdit">Edit</button>
                     </Link>
                     
                     <DeleteOutline className="productListDelete" onClick={()=>handleDelete(parans.row.id)}/>

                     </>
                )
           }

      },
   
  ];
  
    return (
        <div className="productList">
            <DataGrid rows={data} disableSelectionOnClick columns={columns} pageSize={10} checkboxSelection />

        </div>
    )
}
